/* All your CSS goes here */

body {
	padding-top: 60px;
}

.img-to-inline-block {
	display: inline-block;
}

.metering-panel {
	width: 697px;
	margin-bottom: 0px;
}

.panel-default>.metering-panel-heading {
	background-color: #bbc9cf;
	color: #ffffff;
	font-size: 18px;
	height: 133px;
}

.metering-body-heading {
	color: #666666;
	font-size: 24px;
	//padding-left: 40px;
	margin-top: 90px;
}

.metering-body-text {
	color: #666666;
	font-size: 15px;
	margin-bottom: 15px;
	margin-left: 58px;
}

.metering-subscribe-button > button {
	border-radius: 0px;
	border-width: 1 1 1 1px;
	border-color: #b9c841;
	background-color: #b9c841;
	color: #ffffff;
	width: 148px;
	height: 32px;
	font-size: 14px;
	margin-bottom: 32px;
	margin-left: 58px;
}

.metering-login-button > button {
	border-radius: 0px;
	border-width: 1 1 1 1px;
	border-color: #b9c841;
	background-color: #b9c841;
	color: #ffffff;
	width: 148px;
	height: 32px;
	font-size: 14px;
	margin-bottom: 10px;
	margin-left: 58px;
}

.metering-keepbrowsing-button > button {
	border-radius: 0px;
	border-width: 1 1 1 1px;
	border-color: #bbc9cf;
	color: #97a1a6;
	width: 148px;
	height: 32px;
	font-size: 14px; 
	margin-left: 58px;
	margin-bottom: 56px;
}
